<template>
  <div>
    <h2>Chat Room</h2>

      <div class="readMessage">
        <ul>
          <li v-for="message in messages" :key="message.id">
            <span id="name">{{ message.name }}</span>
            <span id="content">{{ message.content }}</span>
          </li>
        </ul>
      </div>
      
      <div class="sendMessage">
          <form @submit.prevent="addMessage">          
           <input type="text" v-model="newMessage">
           <button>전송</button>
      </form>
      </div>

  </div>
</template>

<script>
import db from '@/firebase/init'

export default {
  name: 'Chat',
  props: ['name'],
  data(){
    return{
      messages: [],
      newMessage: null
    }
  },
  methods: {
    addMessage(){
      console.log(this.newMessage);
      if(this.newMessage){
          db.collection('messages').add({
              content: this.newMessage,
              name: this.name
          }).catch(err => {
              console.log(err)
          })
          this.newMessage = null
      }
    }
  },
  created(){
      let ref = db.collection('messages')
      
      ref.onSnapshot(snapshot => {
          snapshot.docChanges().forEach(change => {
              if(change.type == 'added'){
                  let doc = change.doc
                  this.messages.push({
                      id: doc.id,
                      name: doc.data().name,
                      content: doc.data().content
                  })
              }
          })
      })        
  }
}
</script>

<style>
.readMessage{
  height: 500px;
  width: 300px;
  border: 1px solid black;  
  margin: auto;
}

ul{
  list-style: none;
  margin:0px;
  padding:0px;
  text-align:left;
}

#content{    
  display: inline-block;
  position: relative;    
	word-wrap: break-word;	
  background: #3c81fa;
	max-width: 300px;
	padding:5px;
  border-radius: 10px;
}

#name{
  font-weight: bold;
  display: block;
}
</style>


