<template>
  <div>
    <div>
      <div>
        <h2>Welcome Chat</h2>
        <form @submit.prevent="enterChat">
          <input type="text" name="name" v-model="name" placeholder="닉네임 입력">
          <button>시작하기</button>
          <p v-if="feedback">{{ feedback }}</p>
        </form>
      </div>
    </div>    
  </div>
</template>

<script>
export default {
  name: 'Welcome',
  data(){
    return{
      name: null,
      feedback: null
    }
  },
  methods: {
    enterChat(){
      if(this.name){
        this.$router.push({ name: 'Chat', params: { name: this.name }})
      } else {
        this.feedback = "닉네임이 필요합니다."
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



